.button {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  padding: 0 24px;
  min-width: 180px;
  height: 46px;
  line-height: 16px;
  letter-spacing: 0.1em;
  text-transform: uppercase;
  font-family: GothamPro, sans-serif;
  font-weight: 500;
  font-size: 12px;
  color: #424242;
  background-color: transparent;
  border-width: 2px;
  border-style: solid;
  border-color: transparent;
  opacity: 0.85;
  transition: 0.2s opacity;
}
.button:hover {
  opacity: 1;
}
